import styles from './rejectContract.module.scss';
import RejectButton from '../../../ui/buttons/BuyButton/BuyButton';
import { ReactSVG } from 'react-svg';
import rejectIcon from '../icons/rejectIcon.svg';
import { rejectContractThunk } from '../../../modules/contracts/rejectContract/rejectContractThunk';
import { useDispatch } from 'react-redux';
import { useState } from 'react';

interface Props {
  toggleRejectBlock: () => void;
  id: string;
}

export const RejectContract = ({toggleRejectBlock, id}: Props) => {
  const dispatch = useDispatch()
  const [isFirstRulesAgree, setIsFirstRulesAgree] = useState(false);
  const [isSecondRulesAgree, setIsSecondRulesAgree] = useState(false);
  const [isThirdRulesAgree, setIsThirdRulesAgree] = useState(false);
  const isAgreeWithRules = isFirstRulesAgree && isSecondRulesAgree && isThirdRulesAgree;

  const rejectClick = (id: string) => {
    dispatch(rejectContractThunk({
      contract_id: id,
    }));
    toggleRejectBlock();
  }
  
  return (
    <div className={styles.rejectContract}>
      <div className={styles.headerBlock}>
        <h2 className={styles.title}> </h2>
        <ReactSVG className={styles.rejectIcon} src={rejectIcon} onClick={() => toggleRejectBlock()}/>
      </div>
      <p className={styles.info}>         ,     ManGo.Trade    ,  :</p>
        <div className={styles.block}>
        <div className={styles.checkBoxWrapper}>
            <input
                className={styles.checkBox}
                type='checkbox'
                onClick={() => setIsFirstRulesAgree(!isFirstRulesAgree)}
            />
        </div>
           <p className={styles.rule}>
                   60  .
           </p>
        </div>
        <div className={styles.block}>
          <div className={styles.checkBoxWrapper}>
            <input
                className={styles.checkBox}
                type='checkbox'
                onClick={() => setIsSecondRulesAgree(!isSecondRulesAgree)}
            />
          </div>
            <p className={styles.rule}>
                      70%   .
           </p>

        </div>
        <div className={styles.block}>
          <div className={styles.checkBoxWrapper}>
            <input
                className={styles.checkBox}
                type='checkbox'
                onClick={() => setIsThirdRulesAgree(!isThirdRulesAgree)}
            />
            </div>
            <p className={styles.rule}>
                        30%   .
           </p>

        </div>
        <div className={styles.buttonBlock}>
          <RejectButton disabled={!isAgreeWithRules} onClick={() => rejectClick(id)} color='green'>
            
          </RejectButton>
        </div>
        <div className={styles.fakeInfoButton}>
        </div>
    </div>
  )
}
